.i{
    display: flex;
    height: 100vh;
    background-color: #eee;
}

.i-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.i-right{
    flex: 1;
    position: relative;
}
.i-left-wrapper{
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.i-name{
    font-size: 60px;
    
}

.i-intro{
    font-size: 30px;
}

.i-title{
    height: 55px;
    overflow: hidden;
}

.i-title-wrapper{
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
    50%{
        transform: translateY(-50px);
    }
    100%{
        transform: translateY(-100px);
    }
}

.i-title-item{
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: #01579b;
    display: flex;
    align-items: center;
}

.i-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    clip-path: polygon(100% 0%, 90% 51%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    z-index: 2;
}

.i-bg{
    clip-path: polygon(73% 0, 53% 50%, 77% 100%, 0 100%, 35% 50%, 0 0);
    background-color: #01579b;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
}